.registration-page{padding:40px 0}
.reg-card{margin-top:20px;padding:20px;border-radius:12px;background:#fff;box-shadow:0 6px 18px rgba(31,41,55,0.04)}
.field-grid{display:grid;grid-template-columns:1fr 1fr;gap:12px}
.field-grid label{display:flex;flex-direction:column;font-size:14px}
.field-grid input{padding:10px;border:1px solid #e6e9ef;border-radius:8px}
.reg-actions{display:flex;justify-content:flex-end;gap:12px;margin-top:18px}

@media (max-width:900px){
  .field-grid{grid-template-columns:1fr}
}
