.welcome-page{
  min-height: calc(100vh - 110px);
  display:flex;align-items:center;padding:40px 0;
  background:linear-gradient(135deg, rgba(102,126,234,0.06), rgba(118,75,162,0.03));
}
.welcome-grid{display:grid;grid-template-columns:1fr 520px;gap:48px;align-items:center}
.welcome-left{padding:8px 0}
.step-indicator{color:var(--color-neutral);font-weight:700;margin-bottom:6px}
.welcome-title{font-size:44px;line-height:1.02;margin:6px 0}
.welcome-desc{font-size:16px;color:var(--color-neutral);margin-top:12px}
.welcome-actions{margin-top:26px;display:flex;gap:14px}

.welcome-right{display:flex;justify-content:center;align-items:center}
.logo-wrap{width:480px;height:480px;border-radius:28px;background:linear-gradient(135deg, rgba(102,126,234,0.12), rgba(118,75,162,0.08));display:flex;align-items:center;justify-content:center;box-shadow:0 16px 60px rgba(118,75,162,0.08);}
.rah-logo{width:300px;height:300px;animation:float 6s ease-in-out infinite;border-radius:8px}

@keyframes float{
  0%{transform:translateY(0) rotate(0deg)}
  50%{transform:translateY(-14px) rotate(2deg)}
  100%{transform:translateY(0) rotate(0deg)}
}

/* Responsive adjustments */
@media (max-width: 900px){
  .welcome-grid{grid-template-columns:1fr;padding:0 0 30px}
  .logo-wrap{width:260px;height:260px}
  .rah-logo{width:180px;height:180px}
  .welcome-title{font-size:28px}
}

