.step-dots{display:flex;gap:8px;flex-wrap:wrap;margin:12px 0}
.step-dots .dot{display:flex;align-items:center;gap:8px;padding:8px 12px;border-radius:999px;background:transparent;color:var(--color-neutral);border:1px solid rgba(31,41,55,0.04)}
.step-dots .dot.active{background:linear-gradient(90deg,var(--color-primary),var(--color-secondary));color:white;border:0}
.step-dots .dot.done{background:rgba(16,185,129,0.08);color:var(--color-success);border:0}
.dot-index{font-weight:700;display:inline-block;width:22px;height:22px;border-radius:999px;text-align:center;line-height:22px}
.dot-label{font-size:13px}
