:root{
  --color-primary:#667EEA;
  --color-secondary:#764BA2;
  --color-success:#10B981;
  --color-warning:#F59E0B;
  --color-danger:#EF4444;
  --color-neutral:#6B7280;
  --color-white:#FFFFFF;
  --color-black:#1F2937;
  --font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial;
  --max-width: 1200px;

  /* Typography */
  --h1-size: 48px;
  --h2-size: 36px;
  --base-size: 16px;

  /* Spacing scale */
  --space-xs: 4px;
  --space-sm: 8px;
  --space-md: 16px;
  --space-lg: 24px;
  --space-xl: 40px;

  /* Breakpoints */
  --bp-sm: 480px;
  --bp-md: 768px;
  --bp-lg: 1024px;
}

*{box-sizing:border-box}
html,body,#root{height:100%}
body{
  margin:0;
  font-family:var(--font-family);
  color:var(--color-black);
  background:linear-gradient(180deg, #f7fafc 0%, #ffffff 100%);
  -webkit-font-smoothing:antialiased;
}

.container{
  width:100%;
  max-width:var(--max-width);
  margin:0 auto;
  padding:0 20px;
}

/* layout utilities */
.row{display:flex;gap:var(--space-md);align-items:center}
.stack{display:flex;flex-direction:column;gap:var(--space-md)}

/* Card and glass surfaces */
.card{background:var(--color-white);border-radius:12px;padding:18px;box-shadow:0 8px 24px rgba(31,41,55,0.06)}
.glass{background:rgba(255,255,255,0.6);backdrop-filter:blur(6px);border-radius:12px;padding:16px}

.rah-navbar{
  background:transparent;
  padding:12px 0;
  position:sticky;
  top:0;
  z-index:40;
}
.nav-inner{display:flex;align-items:center;justify-content:space-between}
.nav-logo{font-weight:800;color:var(--color-primary);text-decoration:none;font-size:20px}
.nav-links{display:flex;align-items:center;gap:14px}
.nav-links a{color:var(--color-neutral);text-decoration:none;padding:8px 10px;border-radius:8px}
.nav-links a:hover{color:var(--color-primary);background:rgba(102,126,234,0.06)}

/* Mobile nav toggle */
.nav-toggle{display:none;background:transparent;border:0;color:var(--color-primary);font-weight:700}
.mobile-menu{display:none;flex-direction:column;gap:10px;padding:12px}

@media (max-width: 900px){
  .nav-links{display:none}
  .nav-toggle{display:inline-flex}
  .mobile-menu{display:flex}
}

.rah-footer{padding:28px 0;text-align:center;color:var(--color-neutral);border-top:1px solid rgba(31,41,55,0.04);background:linear-gradient(180deg, rgba(118,75,162,0.02), transparent)}

.btn{border-radius:10px;padding:12px 18px;font-weight:700;border:0;cursor:pointer;font-size:15px;transition:all .18s ease}
.btn.primary{background:linear-gradient(90deg,var(--color-primary),var(--color-secondary));color:white;box-shadow:0 8px 20px rgba(102,126,234,0.12)}
.btn.ghost{background:transparent;border:1px solid rgba(31,41,55,0.06);color:var(--color-black)}
.btn.ghost:hover{background:rgba(118,75,162,0.04)}

.btn.small{padding:8px 12px;font-size:14px;border-radius:8px}

/* headings */
.h1{font-size:var(--h1-size);line-height:1.05;margin:6px 0}
.h2{font-size:var(--h2-size);margin:6px 0}

/* utilities */
.muted{color:var(--color-neutral)}


/* utility */
.welcome-title{font-size:42px;margin:12px 0 10px}
.welcome-desc{font-size:16px;color:var(--color-neutral);line-height:1.5}
